import React, { useState } from 'react';
import { motion } from 'framer-motion';

interface AIAssistantProps {
  className?: string;
}

export const AIAssistant: React.FC<AIAssistantProps> = ({ className }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  return (
    <motion.div
      className={`ai-assistant ${className}`}
      initial={{ opacity: 0, scale: 0.8 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.3 }}
    >
      <div 
        className="ai-assistant-header"
        onClick={() => setIsExpanded(!isExpanded)}
        style={{
          backgroundColor: '#0A0A0F',
          border: '1px solid #00FFFF',
          borderRadius: '8px',
          padding: '12px',
          cursor: 'pointer',
          display: 'flex',
          alignItems: 'center',
          gap: '8px',
          color: '#00FFFF'
        }}
      >
        <div 
          style={{
            width: '8px',
            height: '8px',
            borderRadius: '50%',
            backgroundColor: '#00FF94',
            animation: 'pulse 2s infinite'
          }}
        />
        <span>AI Assistant</span>
        <span style={{ marginLeft: 'auto', fontSize: '12px' }}>
          {isExpanded ? '' : ''}
        </span>
      </div>
      
      {isExpanded && (
        <motion.div
          initial={{ height: 0, opacity: 0 }}
          animate={{ height: 'auto', opacity: 1 }}
          exit={{ height: 0, opacity: 0 }}
          style={{
            backgroundColor: '#0A0A0F',
            border: '1px solid #00FFFF',
            borderTop: 'none',
            borderRadius: '0 0 8px 8px',
            padding: '16px',
            color: '#CCCCCC'
          }}
        >
          <div style={{ fontSize: '14px', marginBottom: '12px' }}>
            AI Assistant is ready to help with:
          </div>
          <ul style={{ fontSize: '12px', listStyle: 'none', padding: 0, margin: 0 }}>
            <li style={{ marginBottom: '4px' }}> System optimization</li>
            <li style={{ marginBottom: '4px' }}> Error diagnosis</li>
            <li style={{ marginBottom: '4px' }}> Performance analysis</li>
            <li style={{ marginBottom: '4px' }}> Task automation</li>
          </ul>
          
          <div style={{ 
            marginTop: '12px',
            padding: '8px',
            backgroundColor: '#1A1A2E',
            borderRadius: '4px',
            fontSize: '12px',
            color: '#00FFFF'
          }}>
            Status: Available
          </div>
        </motion.div>
      )}
      
      <style>{`
        @keyframes pulse {
          0% { opacity: 1; }
          50% { opacity: 0.5; }
          100% { opacity: 1; }
        }
      `}</style>
    </motion.div>
  );
};