// API client for Royal Equips Orchestrator backend
class APIClient {
  private baseURL: string;

  constructor() {
    this.baseURL = import.meta.env.VITE_API_BASE_URL || window.location.origin;
  }

  async request<T>(endpoint: string, options?: RequestInit): Promise<T> {
    const url = `${this.baseURL}${endpoint}`;
    
    const response = await fetch(url, {
      headers: {
        'Content-Type': 'application/json',
        ...options?.headers,
      },
      ...options,
    });

    if (!response.ok) {
      throw new Error(`API request failed: ${response.status} ${response.statusText}`);
    }

    return response.json();
  }

  // Health and status endpoints
  async getHealth() {
    return this.request('/healthz');
  }

  async getMetrics() {
    return this.request('/metrics');
  }

  async getReadiness() {
    return this.request('/readyz');
  }

  // Agent endpoints
  async getAgents() {
    return this.request('/agents');
  }

  async createAgentSession() {
    return this.request('/agents/session', { method: 'POST' });
  }

  // Job endpoints
  async getJobs() {
    return this.request('/jobs');
  }

  // Control endpoints
  async getControlStatus() {
    return this.request('/api/control/status');
  }

  async toggleGodMode() {
    return this.request('/api/control/god-mode', { method: 'POST' });
  }

  async emergencyStop() {
    return this.request('/api/control/emergency-stop', { method: 'POST' });
  }

  async resetEmergency() {
    return this.request('/api/control/reset-emergency', { method: 'POST' });
  }

  // GitHub endpoints
  async getGitHubStatus() {
    return this.request('/github/status');
  }

  async getGitHubCommits() {
    return this.request('/github/commits');
  }

  // Shopify endpoints
  async getShopifyStatus() {
    return this.request('/shopify/status');
  }

  async syncShopifyProducts() {
    return this.request('/shopify/sync/products', { method: 'POST' });
  }

  async syncShopifyInventory() {
    return this.request('/shopify/sync/inventory', { method: 'POST' });
  }
}

export const apiClient = new APIClient();
export default apiClient;