import { io, Socket } from 'socket.io-client';

class WebSocketService {
  private socket: Socket | null = null;
  private wsURL: string;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;

  constructor() {
    this.wsURL = import.meta.env.VITE_WS_URL || window.location.origin;
  }

  connect(): Socket {
    if (this.socket?.connected) {
      return this.socket;
    }

    this.socket = io(this.wsURL, {
      transports: ['websocket', 'polling'],
      upgrade: true,
      rememberUpgrade: true,
    });

    this.setupEventHandlers();
    return this.socket;
  }

  private setupEventHandlers() {
    if (!this.socket) return;

    this.socket.on('connect', () => {
      console.log('WebSocket connected');
      this.reconnectAttempts = 0;
    });

    this.socket.on('disconnect', () => {
      console.log('WebSocket disconnected');
    });

    this.socket.on('connect_error', (error) => {
      console.error('WebSocket connection error:', error);
      this.handleReconnect();
    });

    this.socket.on('error', (error) => {
      console.error('WebSocket error:', error);
    });
  }

  private handleReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      console.log(`Attempting to reconnect (${this.reconnectAttempts}/${this.maxReconnectAttempts})`);
      setTimeout(() => {
        this.connect();
      }, 1000 * this.reconnectAttempts); // Exponential backoff
    }
  }

  disconnect() {
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
  }

  // Subscribe to specific channels
  subscribeToSystemMetrics(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('system_metrics', callback);
  }

  subscribeToShopifyEvents(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('shopify_event', callback);
  }

  subscribeToLogs(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('log_entry', callback);
  }

  subscribeToAgentStatus(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('agent_status', callback);
  }

  subscribeToJobUpdates(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('job_update', callback);
  }

  subscribeToAlerts(callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on('alert', callback);
  }

  // Generic event subscription
  on(event: string, callback: (data: any) => void) {
    if (!this.socket) this.connect();
    this.socket?.on(event, callback);
  }

  off(event: string, callback?: (data: any) => void) {
    this.socket?.off(event, callback);
  }

  emit(event: string, data?: any) {
    this.socket?.emit(event, data);
  }

  get connected(): boolean {
    return this.socket?.connected || false;
  }

  getSocket(): Socket | null {
    return this.socket;
  }
}

export const webSocketService = new WebSocketService();
export default webSocketService;