import React, { useState } from 'react';
import { motion } from 'framer-motion';

interface WorkspaceSwitcherProps {
  className?: string;
}

export const WorkspaceSwitcher: React.FC<WorkspaceSwitcherProps> = ({ className }) => {
  const [selectedWorkspace, setSelectedWorkspace] = useState('production');

  const workspaces = [
    { id: 'production', name: 'Production', color: '#00FF94' },
    { id: 'staging', name: 'Staging', color: '#FFB800' },
    { id: 'development', name: 'Development', color: '#FF4757' }
  ];

  return (
    <motion.div
      className={`workspace-switcher ${className}`}
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
    >
      <div style={{
        backgroundColor: '#0A0A0F',
        border: '1px solid #00FFFF',
        borderRadius: '8px',
        padding: '16px',
        color: '#00FFFF'
      }}>
        <div style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '12px' }}>
          Workspace
        </div>
        
        <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
          {workspaces.map((workspace) => (
            <motion.button
              key={workspace.id}
              onClick={() => setSelectedWorkspace(workspace.id)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              style={{
                backgroundColor: selectedWorkspace === workspace.id ? workspace.color : '#1A1A2E',
                color: selectedWorkspace === workspace.id ? '#0A0A0F' : '#CCCCCC',
                border: `1px solid ${workspace.color}`,
                borderRadius: '4px',
                padding: '8px 12px',
                fontSize: '12px',
                fontWeight: 'bold',
                cursor: 'pointer',
                transition: 'all 0.2s ease'
              }}
            >
              {workspace.name}
            </motion.button>
          ))}
        </div>
        
        <div style={{
          marginTop: '12px',
          fontSize: '12px',
          color: '#CCCCCC',
          display: 'flex',
          alignItems: 'center',
          gap: '8px'
        }}>
          <div 
            style={{
              width: '8px',
              height: '8px',
              borderRadius: '50%',
              backgroundColor: workspaces.find(w => w.id === selectedWorkspace)?.color || '#00FFFF',
              animation: 'pulse 2s infinite'
            }}
          />
          Active: {workspaces.find(w => w.id === selectedWorkspace)?.name}
        </div>
      </div>
      
      <style>{`
        @keyframes pulse {
          0% { opacity: 1; }
          50% { opacity: 0.5; }
          100% { opacity: 1; }
        }
      `}</style>
    </motion.div>
  );
};